/* 全局函数 */
window.$globalmethod = {
  // 判空
  isEmpty(arg) {
    if (arg !== '' && arg !== undefined && arg !== null && JSON.stringify(arg) !== '{}' && JSON.stringify(arg) !== '[]') {
      return typeof arg === 'number' ? isNaN(arg) : false;
    }
    return true;
  },
  // 扁平化数组
  flat(arr, key) {
    return arr.reduce((pre, value) => {
      return Array.isArray(value[key]) ? [...pre, ...this.flat(value[key])] : [...pre, value]
    }, [])
  },
  // 获取数组对象第一个度为0的结点
  leafzero(arr, key, node) {
    for (let i=0; i<arr.length; i++) {
      node.push(arr[i]);
      if (Array.isArray(arr[i][key]) && arr[i][key].length>0) {
        return this.leafzero(arr[i][key], key, node);
      }
      return node;
    }
  },
  // 树转链
  leaflist(arr, key, childkey, list) {
    for (let i=0; i<arr.length; i++) {
      if (Array.isArray(arr[i])) {
        this.leaflist(arr[i], key, childkey, list);
      } else if (!arr[i][childkey]) {
        list.push({...arr[i], pkey: arr[i].pkey || arr[i][key]});
      } else {
        list.push({...arr[i], pkey: arr[i].pkey || arr[i][key]});
        let newarr = arr[i][childkey].flatMap(item => {
          return {...item,
            pkey: item.pkey || arr[i][key]
          }
        });
        this.leaflist(newarr, key, childkey, list);
      }
    }
    return list;
  },
  // 获得指定节点链
  nodelist(arr, leaf, key, pkey, list) {
    let node = arr.find(item => {
      return leaf[key] === item[key];
    });
    list.unshift(node);
    if (node[key] !== node[pkey]) {
      let nodes = arr.find(item => {
        return node[pkey] === item[key];
      });
      return this.nodelist(arr, nodes, key, pkey, list);
    }
    return list;
  },
  // 判断对象是否相等
  isEqual(obj1, obj2) {
    let aProps = Object.getOwnPropertyNames(obj1);
    let bProps = Object.getOwnPropertyNames(obj2);
    if (aProps.length !== bProps.length) {
         return false;
    }
    for (let i = 0; i < aProps.length; i++) {
      let propName = aProps[i]
      let propA = obj1[propName]
      let propB = obj2[propName]
      if ((typeof (propA) === 'object')) {
        if (this.isObjectValueEqual(propA, propB)) {
            return true
          } else {
            return false
          }
      } else if (propA !== propB) {
        return false
      } else {
        return true
      }
    }
  }
};
