import './index.less';
import React from 'react';
import { HashRouter as Router, Route, Redirect, Switch, withRouter } from 'react-router-dom';
import { renderRoutes } from 'react-router-config'
import { Spin } from 'antd';

const suspense = (filename) => {
  return React.lazy(() => import(`components/content/${filename}/index.jsx`));
},
Config = [{
  path: '/',
  exact: true,
  component: React.lazy(() => import('views/login/index.jsx'))
}, {
  path: '/login',
  exact: true,
  component: React.lazy(() => import('views/login/index.jsx'))
}, {
  path: '/layout',
  component: React.lazy(() => import('views/layout/index.jsx')),
  children: [{
    path: '/layout/chart/:part',
    component: suspense('chart')
  }, {
    path: '/layout/table',
    component: suspense('table'),
    children: [{
      path: '/layout/table/static',
      component: suspense('table/static')
    }, {
      path: '/layout/table/active',
      component: suspense('table/active')
    }]
  }, {
    path: '/layout/form',
    component: suspense('form')
  }, {
    path: '/layout/editor',
    component: suspense('editor')
  }, {
    path: '/layout/tool',
    component: suspense('tool')
  }]
}];

class Page extends React.Component {
  render() {
    return (
      <Router>
        <React.Suspense fallback={<div className="pageloading"><Spin tip="由于使用Mock模拟，数据随机性强，可能会重定向至默认页面。。。" /></div>}>
          <Route exact
            path={ this.props.location.pathname ? '/' : '*' }
            render={ ()=> ( <Redirect to={ Config[1].path }/> )} />
          <Switch>
            { renderRoutes(Config) }
          </Switch>
        </React.Suspense>
      </Router>
    )
  }
}

export const RoutersConfig = Config;
export const RoutersPage = withRouter(Page);