import React from 'react';
import { connect } from 'react-redux';
import Store from 'redux/index.js';
import { withRouter } from 'react-router-dom';
import { RoutersPage, RoutersConfig } from 'router/index.jsx';

class LayoutContainer extends React.Component {
  pathDeal = (path) => {
    if (path.indexOf(':') > 0) {
      return path.substring(0, Number(path.indexOf(':'))-1);
    }
    return path;
  };
  componentDeal = (component, path) => {
    return path.length>1 ? component.substring(0, path.length) : component;
  };
  routersPage = () => {
    let node = RoutersConfig.find(item => {
      return this.componentDeal(this.props.location.pathname, this.pathDeal(item.path)) === this.pathDeal(item.path)
    });
    if (!node) {
      return false
    }
    return window.$globalmethod.flat(node.children, 'children');
  };
  routeChange = (info) => {
    let usrInfo = info || JSON.parse(sessionStorage.getItem('usrInfo'));
    if (usrInfo) {
      if(!info) {
        this.props.setInfo('usrInfo', usrInfo);
      };
      this.props.history.replace({pathname: this.props.location.pathname && this.props.location.pathname !== '/login' && this.props.location.pathname !== '/' && this.routersPage() ? this.props.location.pathname : '/layout/chart/2D'});
    }
    else {
      sessionStorage.clear();
      this.props.history.replace({pathname: '/login'});
    }
  };
  constructor(props) {
    super(props);
    Store.subscribe(() => {
      const state = Store.getState();
      this.routeChange(state.usrInfo);
    });
    this.routeChange(this.props.tiger.usrInfo);
  };
  componentDidCatch() {
    this.props.history.push('/');
    window.location.reload();
  };
  render() {
    return (
      <RoutersPage />
    )
  }
};

const mapStateToProps = (state) => {
  return {
    tiger: state
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    setInfo: (type, params) => dispatch({ type: type, params: params })
  }
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(LayoutContainer));
