import { createStore } from 'redux';

const tiger = null;
const reducer = (state = tiger, action) => {
	if (action && action.type) {
    return state = {...state,
    	[action.type]: action.params
    };
  }
  return state;
};

export const method = {
	setInfo: (type, params) => {
	  return {
	  	type: type,
	  	params: params
	  }
	}
};

export default createStore(reducer);
